const DesAdv = () => {
  const OrderData = useSelector((state) => state.desAdv.OrderData)
  const dispatch = useDispatch()

  useEffect(() => {
    axios
      .get(XMLData, {
        'Content-Type': 'application/xml; charset=utf-8',
      })
      .then((response) => {
        var jsonDataFromXml = new XMLParser().parseFromString(
          response.data,
          'text/xml',
        )
        // var jsonDataFromXml = xml2json(response.data)
        let jsonData = jsonDataFromXml.children
        console.log(jsonData)
        console.log(jsonDataFromXml.getElementsByTagName('Name'))
        // const data2 = xml2json(jsonData, 'data')
        // console.log(data2)
        const data = appendData(jsonData, OrderData)
        console.log('dictDesAdv', data)
        dispatch(setOrderData({ data }))
      })
  }, [])

  return (
    <>
      <Header />
      <Title title="Накладная" />
      <InfoBlock title="Точка доставки" />
      <DesBlock />
      <DesAdvTools />
      <DesBlockTable />
    </>
  )
}

function appendData(jsonData, orderData) {
  // const orderData = OrderDataS.slice()
  let dictDesAdv = {}

  jsonData.forEach((el) => {
    if (el.value) {
      // console.log(el.name, '-', el.value)
      // console.log(el.value)
      dictDesAdv[el.name] = el.value
    } else {
      if (el.name === 'Options') {
        console.log('name === Options', el.name)
        console.log('Options', el.children[0].children)
        const options = iterableOptions(el.children[0].children)
        // console.log('options', options)
        dictDesAdv = { ...dictDesAdv, ...options }
        console.log('dictDesAdv', dictDesAdv)
      }
    }
  })
  // console.log('dictDesAdv', dictDesAdv)
  let data = []

  function iterableData(orderData) {
    orderData.forEach((el) => {
      const name = el ? el.name : ''
      if (dictDesAdv[name]) {
        // console.log('dictDesAdv[name', dictDesAdv[name])
        if (name.includes('Date')) {
          let newDate = dictDesAdv[name]
          // console.log('newDate', newDate)
          newDate = newDate
            ? newDate.slice(0, 4) +
              '-' +
              newDate.slice(4, 6) +
              '-' +
              newDate.slice(-2)
            : ' '
          // console.log('newDate', newDate)
          data.push({
            ...el,
            value: newDate,
          })
        } else {
          data.push({
            ...el,
            value: dictDesAdv[name],
          })
        }
      } else {
        console.log('ee', el)
      }
    })
    console.log('data3', data)
    return data
  }

  return iterableData(orderData)

  // console.log('orderData', orderData)
  // console.log('data', data)
}

function iterableOptions(orderData) {
  let data = []
  const dictDesAdvOptions = {}
  orderData.forEach((el) => {
    // const name = el ? el.name : ''
    // console.log('el', el.children[0].value, el.children[1].value)
    dictDesAdvOptions[el.children[0].value] = el.children[1].value
  })
  // console.log('dictDesAdvOptions', dictDesAdvOptions)
  return dictDesAdvOptions
}

